import React from "react";
import { Link } from "react-router-dom";

function AuthNav() {
  function resolveClassName(linkText) {
    let currentPath = window.location.pathname;
    return currentPath === linkText ? "pd-24x pd-12y current" : "pd-24x pd-12y";
  }
  return (
    <nav id="auth-nav" className="nav-t1">
      <ul className="mr-32b">
        <li>
          <Link to="/register" className={resolveClassName("/register")}>
            Register
          </Link>
        </li>
        <li>
          <Link to="/login" className={resolveClassName("/login")}>
            Login
          </Link>
        </li>
      </ul>
    </nav>
  );
}

export default AuthNav;
