export function authorization(
  isAdmin,
  checkAdminUser,
  checkRegularUser,
  isRegularAuth,
  setAuth
) {
  if (isRegularAuth) {
    setAuth(true);
  } else if (isAdmin && localStorage.getItem("token")) {
    checkAdminUser();
  } else if (localStorage.getItem("token")) {
    checkRegularUser();
  } else if (isRegularAuth) {
    setAuth(true);
  }
}
