import React, { useState, useEffect } from "react";
import { useStoreState, useStoreActions } from "easy-peasy";
import Register from "./Register/Register";
import { Redirect } from "react-router-dom";
import Login from "./Login/Login";
import WelcomeSection from "./WelcomeSection/WelcomeSection";
import "./auth.css";
import { Switch, Route } from "react-router-dom";
import AuthNav from "./AuthNav/AuthNav";
import { authorization } from "./authorization";

function Auth() {
  let [email, setEmail] = useState("");
  let [password, setPassword] = useState("");
  let [cpassword, setCPassword] = useState("");
  let isAuth = useStoreState(state => state.authMod.isAuth);
  let isAdmin = useStoreState(state => state.authMod.isAdminAttempt);
  let { type: isRegularUserAuth } = useStoreState(
    state => state.profileMod.profile
  );
  let checkAdmin = useStoreActions(action => action.authMod.FETCH_ALL_USERS);
  let setAuth = useStoreActions(action => action.authMod.SET_REGULAR_AUTH);
  let checkRegularUser = useStoreActions(
    action => action.profileMod.FETCH_PROFILE_START
  );
  let loginUser = useStoreActions(action => action.authMod.FETCH_LOGIN_START);
  let registerUser = useStoreActions(
    action => action.authMod.FETCH_REGISTER_START
  );
  useEffect(() => {
    if (!isAuth) {
      authorization(
        isAdmin,
        checkAdmin,
        checkRegularUser,
        isRegularUserAuth,
        setAuth
      );
    }
  }, [isAuth, isRegularUserAuth]);
  if (!isAuth)
    return (
      <section id="auth" className="disp-flex">
        <WelcomeSection />
        <form
          id="auth-register"
          action=""
          className="flex-basis-50-l pd-64x pd-64t bg-light pd-64b"
          noValidate={true}
          onSubmit={e => {
            e.preventDefault();
            if (window.location.pathname === "/register")
              registerUser({ email, password, cpassword });
            else loginUser({ email, password });
          }}
        >
          <h2 className="prim-text-clr">Register your account</h2>
          <p className="lh-16 mr-16t mr-32b">
            Please enter the same email as entered in the AtarV-Ed membership
            form.
          </p>
          <AuthNav />
          <Switch>
            <Route path="/register">
              <Register
                setEmail={setEmail}
                email={email}
                setPassword={setPassword}
                setCPassword={setCPassword}
                cpassword={cpassword}
                password={password}
              />
            </Route>
            <Route path="/login">
              <Login
                setEmail={setEmail}
                email={email}
                setPassword={setPassword}
                password={password}
              />
            </Route>
          </Switch>
        </form>
      </section>
    );
  else
    return (
      <Redirect
        to={
          window.location.pathname === "/register"
            ? "/main/settings"
            : "/main/dashboard"
        }
      />
    );
}

export default Auth;
