import React, { useState } from "react";
import { useStoreActions, useStoreState } from "easy-peasy";
function Login({ email, setEmail, password, setPassword }) {
  let errors = useStoreState(state => state.authMod.errors);
  let setErrors = useStoreActions(action => action.authMod.CLEAR_ERRORS);
  let pwdError = errors.find(err => err.type === "password");
  let emailError = errors.find(err => err.type === "email");
  let iwdCred = errors.find(err => err.type === "Invalid Credentials");
  return (
    <>
      <label className="mr-16t form-labl" htmlFor="email">
        Email
      </label>
      <input
        className="mr-8b form-inp wi-300"
        type="email"
        name="email"
        id="email"
        value={email}
        onChange={e => {
          setEmail(e.target.value);
          console.log(errors);
          if (errors.length) setErrors([]);
        }}
        placeholder="Enter your email"
      />
      {emailError ? <p className="form-hint">{emailError.message}</p> : null}
      <label className="mr-16t form-labl" htmlFor="password">
        Password
      </label>
      <input
        className="mr-8b form-inp wi-300"
        type="password"
        name="password"
        id="password"
        value={password}
        onChange={e => {
          setPassword(e.target.value);
          if (errors.length) setErrors([]);
        }}
        placeholder="Enter your password"
      />
      {pwdError ? <p className="form-hint">{pwdError.message}</p> : null}
      <input
        className="mr-16t mr-8b form-inp wi-300"
        type="submit"
        value="Submit"
      />

      {iwdCred ? <p className="form-hint">{iwdCred.message}</p> : null}
    </>
  );
}

export default Login;
